<template>
    <div class="list-page" ref="listBox">
         <!-- 头 -->
        <!-- <van-nav-bar title="我的战绩" fixed :border="false"/> -->
        <div class="list-page-fixed">
            <div class="list-page-header">
                <div style="height:40px;"></div>
                <div>我的战绩</div>
            </div>

            <div class="list-top">
                <div class="list-top-img">
                    <img src="../assets/imgs/list-top.png" alt="">
                </div>
                <div class="list-top-content">
                    <div class="list-top-content-title">今日总收益</div>
                    <div class="list-top-content-money">{{profit}}蚪金</div>
                </div>
            </div>

            <!-- tab栏 -->
            <div class="list-tabs">
                <div class='list-tabs-item' :class="{'list-tabs-item-active':tabActive == 1}" @click="tabClick(1)">
                    <span>猜涨跌</span>
                </div>
                <div class='list-tabs-item' :class="{'list-tabs-item-active':tabActive == 2}" @click="tabClick(2)">
                    <span>幸运转盘</span>
                </div>
            </div>
        </div>

        <div class="list-space"></div>
        <!-- 幸运转盘 -->
        <div class="list-tab-box" v-if="tabActive == 2">
            <div class="list-tab-content">
                <div class="list-tab-content-title">
                    <img src="../assets/imgs/list-record.png" alt="">
                    <span>历史战绩</span>
                </div>
                <div class="list-tab-content-list">
                    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getDrawList">
                        <div v-for="(item,index) in drawList" :key="index" class='list-tab-content-list-item'>
                            <div class="list-tab-content-list-item-text">
                                <div class="list-tab-content-list-item-text-left">幸运转盘</div>
                                <div class="list-tab-content-list-item-text-right">
                                    <span v-if="item.game_raword.id == 3 || item.game_raword.id == 4 || item.game_raword.id == 5 || item.game_raword.id == 7">+{{parseInt(item.game_raword.prize_money)}}{{item.game_raword.prize}}</span>
                                    <span v-if="item.game_raword.id == 1 || item.game_raword.id == 2">{{item.game_raword.prize}}一台</span>
                                    <span v-if="item.game_raword.id == 6 || item.game_raword.id == 8">{{item.game_raword.prize}}</span>
                                </div>
                            </div>
                            <div class="list-tab-content-list-item-tip">
                                <div class="list-tab-content-list-item-tip-left">{{item.add_time}}</div>
                                <div class="list-tab-content-list-item-tip-right">抽奖金额：0.5</div>
                            </div>
                        </div>
                    </van-list>

                    <div class="data-none" v-if="drawList.length == 0">
                        暂无记录
                    </div>
                </div>
            </div>
        </div>
        <!-- 竞猜信息 -->
        <div class="list-tab-box" v-if="tabActive == 1" style="padding-bottom:0">
            <div class="list-tab-content">
                <div class="list-tab-content-title">
                    <img src="../assets/imgs/list-record.png" alt="">
                    <span>当前竞猜</span>
                </div>   
                <!-- 没下注 -->
                <div v-if="!page_data.user_postbtc" class="no-guess" @click="goGuess">
                    <div>您还未参与竞猜哦~距离第{{page_data.btc_result.btc_id + 1}}期竞猜   </div> 
                    <div>截止时间还有<span>{{end_minute}}</span>分<span>{{end_second}}</span>秒</div>
                    <van-icon class="no-guess-arrow" name="arrow" />
                </div> 
                <!-- 已下注 -->
                <div v-if="page_data.user_postbtc" class="have-guess" @click="goGuess">
                    <div class="have-guess-title">
                        {{page_data.user_postbtc.btc_id}}期
                        <span class="have-guess-title-up" v-if="page_data.user_postbtc.type == 1">看涨</span>
					    <span class="have-guess-title-down" v-if="page_data.user_postbtc.type == 2">看跌</span>
                    </div>
                    <div class="have-guess-info">
                        <div class="have-guess-info-left">
                            <img src="../assets/imgs/have-guess-info-left.png" alt="">
                            {{draw_time}}开奖
                        </div>
                        <div class="have-guess-info-right">
                            竞猜金额{{page_data.user_postbtc.dw_money}}
                            <img src="../assets/imgs/have-guess-info-right.png" alt="">
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
        <!-- 猜涨跌 -->
        <div class="list-tab-box" v-if="tabActive == 1">
            <div class="list-tab-content">
                <div class="list-tab-content-title">
                    <img src="../assets/imgs/list-record.png" alt="">
                    <span>历史战绩</span>
                </div>
                <div class="list-tab-content-list">
                    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getGuessList">
                        <div v-for="(item,index) in guessList" :key="index">
                            <div v-if="item.status != 0"  class='list-tab-content-list-item'>
                                <div class="list-tab-content-list-item-text">
                                    <div class="list-tab-content-list-item-text-left">第{{item.btc_id}}期</div>
                                    <div class="list-tab-content-list-item-text-right">
                                        
                                    </div>
                                </div>
                                <div class="list-tab-content-list-item-tip">
                                    <div class="list-tab-content-list-item-tip-left">
                                        {{item.add_time}}
                                        <span v-if="item.type == 1" class="list-tab-content-list-item-tip-middle-up">看涨</span>
                                        <span v-if="item.type == 2" class="list-tab-content-list-item-tip-middle-down">看跌</span>
                                    </div>
                                    <!-- <div class="list-tab-content-list-item-tip-middle">
                                        
                                    </div> -->
                                    <div class="list-tab-content-list-item-tip-right">
                                        <span class="guess-money" v-if="item.status == 1">+{{item.win_dw}}</span>
                                        <span class="guess-money" v-if="item.status == 2">-{{item.dw_money}}</span>
                                        <span class="guess-win" v-if="item.status == 1">胜</span>
                                        <span class="guess-fail" v-if="item.status == 2">负</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </van-list>

                    <div class="data-none" v-if="guessList.length == 0">
                        暂无记录
                    </div>

                </div>
            </div>
        </div>


        <!-- 涨/跌结果 -->
		<!-- <van-popup v-model="showGuessResultPopup">
			<div class="guess-result-box">
				<div class="guess-result-title">
					<div class="guess-result-title-line"></div>
					<div class="guess-result-title-text">
						第{{guess_result.btc_id}}期
						<span v-if="guess_result.type == 1">涨</span>
						<span v-if="guess_result.type == 2">跌</span>
					</div>
					<div class="guess-result-title-line"></div>
				</div>
				<van-icon class="guess-result-close" name="cross" @click="closeGuessResult" />
				<div v-if="guess_result.status == 1" class="guess-result-text">恭喜竞猜正确，您本期获得奖励：</div>
				<div v-if="guess_result.status == 2" class="guess-result-text">
					<div style="padding-bottom:5px;">很遗憾~</div>
					<div>您本期竞猜错误！</div>
				</div>
				<div class="guess-result-img">
					<img v-if="guess_result.status == 1" src="../assets/imgs/guess-succ.png" alt="">
					<img v-if="guess_result.status == 2" src="../assets/imgs/guess-fail.png" alt="">
				</div>
				<div v-if="guess_result.status == 1" class="guess-result-money">
					{{parseFloat(guess_result.win_dy)}}蚪金
				</div>
				<div class="guess-result-btn">
					<img src="../assets/imgs/guess-again.png" @click="closeGuessResult" alt="">
				</div>
				<div v-if="guess_result.status == 1" class="guess-result-tip">
					温馨提示：奖励将稍后直接发放，可在账单中查询
				</div>
				
			</div>
		</van-popup> -->
        
    </div>
</template>



<script>

import { mapState } from 'vuex'
import { mapMutations } from 'vuex'

import { Dialog  } from 'vant';

export default {
    data(){
        return{
            tabActive:1,
            //抽奖记录
            drawList:[],
            guessList:[],
            loading: false,
            finished: false,
            page:1,

            //总的收益
            profit:'',

            timeInterval: '',//当前期数结束时间倒计时
			timeResultInterval: '',//购买期数开奖时间啊in倒计时

			//页面信息
			page_data:{
				btc_result:{
					add_time:"",
					btc_id:"",
					btc_price:"",
					change:"",
					dispaly_name:"",
					
				},
				user_postbtc:{
					add_time:'',
					btc_id:'',
					dw_money:'',
					id:'',
					status:'',
					type:''
				},
				fall:0,
				rank:0,
				risa:0,
				win_count:0,
				win_num:0,
				next_time: '',
				best_money:{
					user_avatar:'',
					user_name:'',
					best_dw:'',
					raword_num:''
				}
            },
            //当前期数结束时间备份
			next_time_tmp: 0,
			//购买期数开奖时间备份
            game_time_tmp:0,
            
            //开奖提示框
			showGuessResultPopup:false,
			//开奖结果
			guess_result:{
				status:2,
				btc_id:1,
				dw_money:'200',
				win_dy:'100',
				type:1
			},
        }
    },
    computed:{
        ...mapState(['user_info']),

        //剩余分钟
		end_minute(){
			let end_minute = parseInt(Math.abs(this.page_data.next_time)/60);
			if(end_minute < 10){
				end_minute = '0' + end_minute;
			}
			return end_minute
		},
		//剩余秒数
		end_second(){
			let end_second = Math.abs(this.page_data.next_time)%60;
			if(end_second < 10){
				end_second = '0' + end_second;
			}
			return end_second
		},
		//开奖时间
		draw_time(){
			let now_date = new Date().getTime();
			let draw_date = new Date(now_date + Math.abs(this.game_time_tmp) * 1000);
            let hour = draw_date.getHours() < 10 ? "0" + draw_date.getHours() : draw_date.getHours();
            let minute = draw_date.getMinutes() < 10 ? "0" + draw_date.getMinutes() : draw_date.getMinutes();
            return hour + ":" + minute;
            // return year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + seconds;
		},
		
    },
    created(){
        this.utils.info('user_info...',this.user_info);
        console.log('kkk',this.$route.query);
        if(this.$route.query && this.$route.query.type){
            this.utils.info('有类型值...');
            this.tabActive = parseInt(this.$route.query.type);
        }

        this.utils.info('当前加载类型...',this.tabActive);


        if(this.$route.params.id){
			this.utils.info('有id...已登录...');
            this.user_info.user_id = this.$route.params.id;
            
            //获取用户信息
		    this.getUserInfo('1');
		}else{
			this.utils.info('没有id...未登录...');
            // this.user_info.user_id = 1;
            
            Dialog.alert({
				title: '系统提示',
				message: '请先登录...'
			}).then(() => {
				// on close
				this.utils.info('返回app...');
				this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
				
			});
        }
        this.utils.info('user_info...',this.user_info);

        
    },
    mounted(){

    },
    methods:{
        //返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
        },
        //获取用户信息 type 1 去请求奖品列表和用户列表 2不去
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
                    this.user_info.token = data.token;
                    this.user_info.free_password = data.free_password;

                    if(type == '1'){
                        

                        //获取总的收益
                        this.getCount();

                        

                        if(this.tabActive == 1){
                            //获取竞猜记录
                            this.getGuessList();

                            //获取竞猜信息
                            this.getPageInfo();
                        }else{
                            //获取转盘抽奖记录
                            this.getDrawList();
                        }
                    }
                    
                }
			})
        },
        //切换类型
        tabClick(index){
            if(this.tabActive == index){
                return false;
            }
            this.tabActive = index;
            this.page = 1;
            this. loading = false;
            this. finished = false;
            if(this.tabActive == 1){
                this.guessList = [];
                this.getGuessList();

                //获取竞猜信息
                this.getPageInfo();
            }else{
                this.drawList = [];
                this.getDrawList();
            }
        },
        //获取总的收益
        getCount(){
            let option = {
                token: this.user_info.token,
            }   
            this.http.post(this.http._HOST + 'wave/count_profit',option).then(result =>{
				this.utils.info('获取总的收益...',result);
				if(result.success){
                    
                    this.profit = result.data.profit;
				}
			})
        },
        //获取转盘抽奖记录
        getDrawList(){
            
            let option = {
                token: this.user_info.token,
                page: this.page
            }
            this.http.post(this.http._HOST + 'turntable/history',option).then(result =>{
				this.utils.info('获取转盘抽奖记录...',result);
				if(result.success){
                    
                    let data = result.data;
                    if(this.page == 1){
                        this.drawList = data;
                    }else{
                        if(data.length == 0){
                            this.finished = true;
                        }else{
                             for(let i=0;i< data.length;i++){
                                this.drawList.push(data[i]);
                            }
                        }
                       
                    }

                    this.page++;
                    
                    
                    this.loading = false;


				}
			})
        },
        //获取竞猜记录
        getGuessList(){
            let option = {
                token: this.user_info.token,
                page: this.page
            }
            this.http.post(this.http._HOST + 'wave/history',option).then(result =>{
				this.utils.info('获取竞猜记录...',result);
				if(result.success){
                    
                    // this.guessList = result.data;
                    let data = result.data;
                    if(this.page == 1){
                        this.guessList = data;
                    }else{
                        if(data.length == 0){
                            this.finished = true;
                        }else{
                             for(let i=0;i< data.length;i++){
                                this.guessList.push(data[i]);
                            }
                        }
                       
                    }

                    this.page++;
                    
                    this.loading = false;


				}
			})   
        },
        //获取竞猜信息
		getPageInfo(){
			let option = {
                token: this.user_info.token
            }
			this.http.post(this.http._HOST + 'wave/show_btc',option).then(result =>{
				this.utils.info('获取页面数据...',result);
				if(result.success){
					this.page_data = result.data;

					
					//先清除定时器
					if(this.timeInterval){
						clearInterval(this.timeInterval);	
					}
					if(this.timeResultInterval){
						clearInterval(this.timeResultInterval);	
					}
					if(this.page_data.user_postbtc){//下注了
						this.page_data.user_postbtc.game_time = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;
						this.game_time_tmp = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;

						//时间倒计时
						this.timeResultInterval = setInterval(() =>{
							this.page_data.user_postbtc.game_time --;
							//this.utils.info('购买期数开奖时间...',this.page_data.user_postbtc.game_time);
							if(this.page_data.user_postbtc.game_time == 0){
								this.utils.info('开奖...',);

								//请求开奖结果
								//this.getDrawResult(this.page_data.user_postbtc.btc_id);
								
								clearInterval(this.timeResultInterval);

								//再次请求，获取新期数信息
								this.getPageInfo();
							}
						},1000)
					}else{
                        
                        //当前展示期数信息
                        this.page_data.next_time = parseInt(Math.abs(this.page_data.next_time)) + 30;
                        this.next_time_tmp = parseInt(Math.abs(this.page_data.next_time)) + 30;
                        //时间倒计时
                        this.timeInterval = setInterval(() =>{
                            this.page_data.next_time --;
                            //this.utils.info('当前期数剩余时间...',this.page_data.next_time);
                            if(this.page_data.next_time == 0){
                                this.utils.info('时间结束，获取新期数信息...',);

                                clearInterval(this.timeInterval);

                                //再次请求，获取新期数信息
                                this.getPageInfo();
                            }
                        },1000)
                    }

					
				}
			})
        },
        //去竞猜页面
        goGuess(){
            this.utils.info('去竞猜页面...');
			this.$bridge.callhandler('goGuess', {tag:"去竞猜页面"}, (data) => {})
        },
        //请求开奖结果
		getDrawResult(btc_id){
			let option = {
				btc_id: btc_id,
				token: this.user_info.token
			}

			this.http.post(this.http._HOST + 'wave/new_result',option).then(result =>{
				this.utils.info('获取开奖结果...',result);
				if(result.success){
					
					this.guess_result = result.data;

					this.showGuessResultPopup = true;
				}
			})

		},
        //关闭竞猜结果
		closeGuessResult(){
			this.showGuessResultPopup = false;
		},
    },
    beforeDestroy(){
        this.utils.warn('页面即将销毁...请清除定时器...');
      
		if(this.timeInterval){
			clearInterval(this.timeInterval);
		}
		if(this.timeResultInterval){
			clearInterval(this.timeResultInterval);
		}
	
	}
}
</script>



<style lang="scss" scoped>
    .list-page{
        background: #f6f6fa;
        width:100%;
        height:100%;

        // .van-nav-bar{
        //     background: transparent;
        //     background: url(../assets/imgs/list-top.png) no-repeat center top;
        //     background-size: 100%;
        // }
        // .van-nav-bar__title{
        //     font-size:18px;
        //     color:#fff;
        // }
        // .van-nav-bar .van-icon{
        //     font-size:20px;
        //     color:#353535;
        // }
        .list-page-fixed{
            position: fixed;
            top:0;
            left:0;
            z-index:19;
        }
        .list-page-header{
            background: url(../assets/imgs/list-top.png) no-repeat center top; 
            background-size: 100%;
            text-align: center;
            font-size:18px;
            color:#fff;
        }

        .list-top{
            position: relative;
        }
        .list-top-img{
            background: #f6f6fa;
        }
        .list-top-img img{
            display: block;
            width:100%;
        }
        .list-top-content{
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
            
        }
        .list-top-content-title{
           font-size:16px;
           color:#FFE9C2;
           text-align: center;
           padding-bottom: 10px;
        }
        .list-top-content-money{
           font-size:24px;
           color:#fff;
           text-align: center;
        }

        .list-tabs{
            display: flex;
            align-items: center;
            background: #f6f6fa;
        }
        .list-tabs-item{
            width:50%;
            height:40px;
            line-height:40px;
            text-align: center;
            font-size:14px;
            color:#999;
            box-sizing: border-box;
        }
        .list-tabs-item-active{
            font-size:16px;
            font-weight: bold;
            color:#333;
        }
        .list-tabs-item-active span{
           display: inline-block;

           border-bottom: 2PX solid #FF8E20;
        }

        .list-tab-box{
            padding:25px 15px;
            background: #f6f6fa;
        }
        .list-tab-content{
            padding:15px;
            border-radius: 15px;
            background: #fff;
        }

        .list-tab-content-title{
            display: flex;
            align-items: center;
        }
        .list-tab-content-title img{
            display: inline-block;
            width:18px;
            margin-right:5px;
        }
        .list-tab-content-title span{
            font-size:16px;
            color:#333;
            font-weight: bold;
        }
        .list-tab-content-list{
            min-height:100px;
        }
        .list-tab-content-list-item{
            padding:15px 0;
            border-bottom: 1PX solid #d5d5d5;
        }
        .list-tab-content-list-item-text{
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size:16px;
            padding-bottom: 10px;
        }
        .list-tab-content-list-item-text-left{
           color:#333;
        }
        .list-tab-content-list-item-text-right{
           color:#FF8E20;
        }
        .list-tab-content-list-item-tip{
            display: flex;
            align-items: center;
            justify-content: space-between;
            color:#999;
        }
        .list-tab-content-list-item-text-left{
            font-size:12px;
            // width:140px;
        }
        .list-tab-content-list-item-text-right{
